body {
  text-align: center;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 200;
}

button {
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 26px;
  outline: none;
  cursor: pointer;
  display: inline-block;
  margin: 10px;
  border-radius: 6px;
}

.add {
  background-color: rgb(88, 172, 132);
}

.double {
  background-color: rgb(152, 90, 163);
}

.half {
  background-color: rgb(71, 116, 168);
}

h1 {
  font-weight: 100;
  font-size: 140px;
}